___CSS_LOADER_EXPORT___.push([module.id, `#main-viewport-container {
    width: 100%;
    height: 100%;
    position: relative;
}

#canvas-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

#control-bar {
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px; /* limita largura dentro da viewport */
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    color: white;
    display: flex;
    gap: 10px;
    flex-wrap: wrap; /* quebra em múltiplas linhas se necessário */
    overflow-x: auto; /* permite rolagem horizontal se ainda exceder */
    max-width: calc(100% - 20px);
    z-index: 10;
    align-items: center;
}

/* Collapsed state: show only a small header row with the collapse button */
#control-bar.collapsed {
    max-height: 44px; /* enough for one row */
    overflow: hidden;  /* hide remaining controls */
    padding-right: 8px;
}

#control-bar #control-bar-toggle {
    font-weight: 700;
}

#control-bar button, #control-bar input, #control-bar label {
    padding: 5px;
    border-radius: 3px;
    border: 1px solid #555;
}

#control-bar button {
    cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,MAAM;IACN,OAAO;AACX;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,UAAU;IACV,WAAW,EAAE,sCAAsC;IACnD,aAAa;IACb,oCAAoC;IACpC,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,SAAS;IACT,eAAe,EAAE,6CAA6C;IAC9D,gBAAgB,EAAE,gDAAgD;IAClE,4BAA4B;IAC5B,WAAW;IACX,mBAAmB;AACvB;;AAEA,2EAA2E;AAC3E;IACI,gBAAgB,EAAE,uBAAuB;IACzC,gBAAgB,GAAG,4BAA4B;IAC/C,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,eAAe;AACnB","sourcesContent":["#main-viewport-container {\n    width: 100%;\n    height: 100%;\n    position: relative;\n}\n\n#canvas-container {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n}\n\n#control-bar {\n    position: absolute;\n    bottom: 10px;\n    left: 10px;\n    right: 10px; /* limita largura dentro da viewport */\n    padding: 10px;\n    background-color: rgba(0, 0, 0, 0.5);\n    border-radius: 5px;\n    color: white;\n    display: flex;\n    gap: 10px;\n    flex-wrap: wrap; /* quebra em múltiplas linhas se necessário */\n    overflow-x: auto; /* permite rolagem horizontal se ainda exceder */\n    max-width: calc(100% - 20px);\n    z-index: 10;\n    align-items: center;\n}\n\n/* Collapsed state: show only a small header row with the collapse button */\n#control-bar.collapsed {\n    max-height: 44px; /* enough for one row */\n    overflow: hidden;  /* hide remaining controls */\n    padding-right: 8px;\n}\n\n#control-bar #control-bar-toggle {\n    font-weight: 700;\n}\n\n#control-bar button, #control-bar input, #control-bar label {\n    padding: 5px;\n    border-radius: 3px;\n    border: 1px solid #555;\n}\n\n#control-bar button {\n    cursor: pointer;\n}"],"sourceRoot":""}]);
